<p>Sabit mıknatıslı senkron motor ve sargılı rotorlu senkron motor modellerinden birisini uzay vektörlü FOC, diğerini de DTC yöntemiyle kontrol etmeniz istenmektedir. Bunun için asenkron motor için yazılmış asenkron_pwm.m dosyası ve DTC klasöründeki dosyaları uygun şekilde düzenleyebilirsiniz. Ancak, senkron motorların en uygun simülasyonları senkron hızla dönen eksenlere (dq) göre olmasına rağmen kontrol yazılımınızın doğrudan dq akım bileşenlerinden ölçüm alması yasaktır; çünkü pratikte bu mümkün değildir. dq akım bileşenleri durgun alfa,beta eksenlerine dönüştürülerek ölçüm olarak alınmalıdır. (Bunun tekrar dq eksenlerine dönüştürülerek kullanılması yazılım için gereksiz bir dönüşüm-ters dönüşüm çifti gibi görülse de ileride ölçümlerde gürültü olduğunu düşündüğünüzde fark önemli olacak. Ama siz yine de şimdilik gürültüsüz çalışacaksınız.) Simülasyonlarda motorlar yüksüz kaldırılacak, hız dengeye gelince yüklenecek, tekrar hız dengeye gelince yük sıfırlanarak tekrar dengeye gelene kadar yeterli bir sürede simülasyon tamamlanacaktır. Akımlar ve hız çizilecektir. Programlarınız ile seçenekleriniz (DTC ile sargılı rotorlu ve FOC ile sabit mıknatıslı ya da DTC ile sabit mıknatıslı ve FOC ile sargılı rotorlu) birbirinizden alındığını düşündürtecek kadar benzememelidir.</p>
